






















import Vue from 'vue'
import { mapGetters } from 'vuex'

export default Vue.extend({
  name: 'Edition',
  props: {
    taskId: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      task: {}
    }
  },
  computed: {
    ...mapGetters(['getTaskById'])
  },
  created() {
    this.task = this.getTaskById(this.taskId)
  }
})
